        } else {
            db.collection('usersDB').doc(user.uid).set({
                        totalCreated: 0
            })
        }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
        
        //document.getElementById("today_date").classList.add("text-danger"); 

        // Now to check if a user is PRO or NOT by checking PRO users database in firebase.
        db.collection('PROusers').doc(user.uid).get().then(doc => {
            if (doc.exists) {
        // console.log("Document data:", doc.data());
