<svelte:head>
    <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css">
        <!-- Appzi: Capture Insightful Feedback -->
        <script async src="https://w.appzi.io/w.js?token=6UPb5"></script>
        <!-- End Appzi -->
</svelte:head>
<style>

*,
*::before,
*::after {
  box-sizing: border-box;
}


img {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

.btn {
  color: #6178f3;
  padding: 0.8rem;
  font-size: 14px;
  text-transform: uppercase;
  border-radius: 4px;
  font-weight: 400;
  display: block;
  width: 100%;
  cursor: pointer;
  border: 1px solid #6178f3;
  background: transparent;
}

.btn:hover{
  color: #6178f3;
  font-weight: bold;

}

.cards {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}

.cards_item {
  display: flex;
  padding: 1rem;
}

@media (min-width: 40rem) {
  .cards_item {
    width: 50%;
  }
}

@media (min-width: 56rem) {
  .cards_item {
    width: 33.3333%;
  }
}

.card {
  background-color: white;
  border-radius: 0.25rem;
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.card_content {
  padding: 1rem;
  /*background: linear-gradient(to bottom left, #EF8D9C 40%, #FFC39E 100%);*/
}

.card_title {
  color: #6178f3;
  font-size: 1.1rem;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin: 10px;
}

.card_text {
  color: #000;
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 1.25rem;    
  font-weight: 400;
}

    .textBlue{
        color: #5778f3;
    }
    .pressDown {
        background-color: #5778f3;
        padding: 10px 30px;
        color: #fff;
        border-radius: 5px;
        border-bottom: 4px solid #3e4caf;
        transition: 0.1s;
    }

    .pressDown:hover{
        border-bottom-width: 0;
        margin-top: 3px;
        color: #fff;
    }

    .pressDownDefault {
        background-color: #707070;
        padding: 10px 30px;
        color: #fff;
        border-radius: 5px;
        border-bottom: 4px solid #35363d;
        transition: 0.1s;
    }

    .pressDownDefault:hover{
        border-bottom-width: 0;
        margin-top: 3px;
        color: #fff;
    }
</style>
<script>
  import { onMount } from 'svelte';
  import { beforeUpdate, afterUpdate } from 'svelte';
  import {Row, Col,Card, Container, Jumbotron, Label, Form, FormGroup, Input, Button, Navbar, NavbarBrand, Nav, NavItem} from 'sveltestrap'
  import { Router, Link, Route } from "svelte-routing";

  import Footer from './Footer.svelte';

  let simpleDesignTemplateImg = "./images/simple-design-template-preview.png"; 
  let aestheticDesignTemplateImg = "./images/aesthetic-design-template-preview.png";
  let blobDesignTemplateImg = "./images/blob-design-template-preview.png";
  let gradientDesignTemplateImg = "./images/gradient-design-template-preview.png";

  let halfRectDesignTemplateImg = "./images/halfrect-design-template-preview.png";
  let rectDesignTemplateImg = "./images/rect-design-template-preview.png";

  let QuotesTwoDesignTemplateImg = "./images/quotes-two-design-template-preview.png";
  let QuotesThreeDesignTemplateImg = "./images/quotes-three-design-template-preview.png";
  let QuotesDesignTemplateImg = "./images/quotes-design-template-preview.png";
  

/*
Authentication check procedure
*/
import { auth, db, firebaseAnalytics } from './firebase';
let authinfo = localStorage.getItem('usrkey');
let user = JSON.parse(authinfo);

let dailyquote='', dailyquoteauthor='', textContent='';
async function dailyQuote() {
    await fetch('https://api.quotable.io/random')
        .then(response => response.json())
        .then(data => {
            textContent = data.content
    })
}

function valuesToLocalStorage(e) {
  e.preventDefault();
  let heading = document.getElementById('heading').value
  localStorage.setItem("heading",JSON.stringify(heading));
  
  let subheading = document.getElementById('subheading').value
  localStorage.setItem("subheading",JSON.stringify(subheading));

  let companyinfo = document.getElementById('companyinfo').value
  localStorage.setItem("companyinfo",JSON.stringify(companyinfo));
  alert('Your values were applied successfully!')
}

    beforeUpdate(() => {
	
	});

    onMount(() => {

    });
</script>
<div>
    <Container fluid>
    <!--{#if user} UNCOMMENT WHEN YOU DONE WITH THIS--> 
	  <Row class="pt-2">
          <Col sm="12" class="p-3">
            <h1 class="textBlue gradient-text font-weight-bold text-center">Templates Library</h1>
          </Col>
          <!--Col sm="12" class="text-center">
            <h4 class="font-weight-bold">Choose any template, Remix the design & start making.</h4>
		      </Col-->
    </Row>
    {#if user}
    <div class="col-sm-12 text-center py-2">
      <Card class="text-center my-2" body>
      <h6 class="textBlue font-weight-bold text-left">Prefill Values</h6>
      <Row>
        <Col lg='6'>
          <p>Input or paste your text copy here & it will be automatically be applied to all the templates! <br/>You will still be able to customize the templates.</p>
					<Button outline color="primary" size='lg' on:click={dailyQuote}>Feed a Random Quote </Button>
        </Col>
        <Col lg='6'>
          <Form on:submit={valuesToLocalStorage}>
          <FormGroup>
            <Input bind:value={textContent} type="text" id='heading' placeholder='Heading i.e. Main Sentence. ' required/>
          </FormGroup>
          <FormGroup>
            <Input type="text" id='subheading' placeholder='Sub-Heading Sentence i.e. Email, Company Name, Call To Action etc.' required/>
          </FormGroup>
          <FormGroup>
            <Input type="text" id='companyinfo' placeholder='Company Name or Social Media Username' required/>
          </FormGroup>
          <Button color='primary' size='lg' type='submit'>Save values</Button>
          </Form>
          </Col>
        </Row>
      </Card>
    </div>
    {/if}
    <ul class="cards">
      
      <li class="cards_item">
        <div class="card">
          <div class="card_image"><img alt="Aesthetic Design Template" src={aestheticDesignTemplateImg} height="300" width="500"></div>
          <div class="card_content">
            <h2 class="card_title textBlue">Aesthetic Design</h2>
            <p class="card_text">Beautiful quote based design for poster and long-form text content.</p>
            <a class="btn" href="/aesthetic-design-template">Edit Template</a>
          </div>
        </div>
      </li>
      <li class="cards_item">
        <div class="card">
          <div class="card_image"><img alt="Blob Design Template" src={blobDesignTemplateImg} height="300" width="500"></div>
          <div class="card_content">
            <h2 class="card_title textBlue">Classic Blob Design</h2>
            <p class="card_text">Blob background based design template with amazing look.</p>
            <a class="btn" href="/blob-design-template">Edit Template</a>
          </div>
        </div>
      </li>
      <li class="cards_item">
        <div class="card">
          <div class="card_image"><img alt="Gradient Design Template" src={gradientDesignTemplateImg} height="300" width="500"></div>
          <div class="card_content">
            <h2 class="card_title textBlue">Gradient Design</h2>
            <p class="card_text">Template design based on gradient background with minimal look.</p>
            <a class="btn" href="/gradient-design-template">Edit Template</a>
          </div>
        </div>
      </li>
      <li class="cards_item">
        <div class="card">
          <div class="card_image"><img alt="Quotes Design Template" src={QuotesDesignTemplateImg} height="300" width="500"></div>
          <div class="card_content">
            <h2 class="card_title textBlue">Quote Template #1</h2>
            <p class="card_text">Minimal template design, best suited for blog post related content.</p>
            <a class="btn" href="/quote-design-template">Edit Template</a>
          </div>
        </div>
      </li>
      <li class="cards_item">
        <div class="card">
          <div class="card_image"><img alt="Quotes 2 Design Template" src={QuotesTwoDesignTemplateImg} height="300" width="500"></div>
          <div class="card_content">
            <h2 class="card_title textBlue">Quote Template #2</h2>
            <p class="card_text">Unique and remixable variation of quotes based design.</p>
            <a class="btn" href="/quote-design-template-two">Edit Template</a>
          </div>
        </div>
      </li>
      <li class="cards_item">
        <div class="card">
          <div class="card_image"><img alt="Quotes 3 Design Template" src={QuotesThreeDesignTemplateImg} height="300" width="500"></div>
          <div class="card_content">
            <h2 class="card_title textBlue">Quote Template #3</h2>
            <p class="card_text">Minimalist yet remixable variation of quotes based design.</p>
            <a class="btn" href="/quote-design-template-three">Edit Template</a>
          </div>
        </div>
      </li>
      <li class="cards_item">
        <div class="card">
          <div class="card_image"><img alt="Half Rectangle Design Template" src={halfRectDesignTemplateImg} height="300" width="500"></div>
          <div class="card_content">
            <h2 class="card_title textBlue">Rectangle Template #1</h2>
            <p class="card_text">With a hint of Rectangle, This template is best for all type of content.</p>
            <a class="btn" href="/rectangle-design-template-two">Edit Template</a>
          </div>
        </div>
      </li>
      <li class="cards_item">
        <div class="card">
          <div class="card_image"><img alt="Rectangle Design Template" src={rectDesignTemplateImg} height="300" width="500"></div>
          <div class="card_content">
            <h2 class="card_title textBlue">Rectangle Template #2</h2>
            <p class="card_text">Another design template with editable and remixable features.</p>
            <a class="btn" href="/rectangle-design-template">Edit Template</a>
          </div>
        </div>
      </li>
      <li class="cards_item">
        <div class="card">
          <div class="card_image"><img alt="Minimal Design Template" src={simpleDesignTemplateImg} height="300" width="500"></div>
          <div class="card_content">
            <h2 class="card_title textBlue">Minimal Design</h2>
            <p class="card_text">Simple and minimal design template suited best for quotes.</p>
            <a class="btn" href="/simple-design-template">Edit Template</a>
          </div>
        </div>
      </li>
    </ul>
        <hr/>
<Footer />
</Container>
</div>